<template>
  <textarea ref="textarea" v-model="content"></textarea>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import CodeMirror from "codemirror";
import "codemirror/addon/lint/lint.css";
import "codemirror/lib/codemirror.css";
import "codemirror/theme/monokai.css";
import "codemirror/mode/yaml/yaml";
import "codemirror/addon/lint/lint";
import "codemirror/addon/lint/yaml-lint";

var editor: undefined | CodeMirror.EditorFromTextArea = undefined;

export default defineComponent({
  name: "Editor",
  props: {
    contextMapYaml: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      content: this.$props.contextMapYaml,
      hasError: false,
    } as {
      editor?: CodeMirror.EditorFromTextArea;
      hasError: boolean;
      content: string;
    };
  },
  updated() {
    if (this.content.normalize() !== this.contextMapYaml.normalize()) {
      this.content = this.contextMapYaml;
      if (editor) {
        const _editor = editor;
        _editor.operation(() => {
          const cursor = _editor.getCursor();
          const viewport = _editor.getViewport();
          _editor.setValue(this.content);
          _editor.setCursor(cursor);
          _editor.scrollIntoView({ ch: cursor.ch, line: viewport.to });
          _editor.refresh();
        });
      }
    }
  },
  mounted() {
    editor = CodeMirror.fromTextArea(this.$el, {
      lineNumbers: true, // display line number
      mode: "text/x-yaml", // grammar model
      gutters: ["CodeMirror-lint-markers"], // Syntax checker
      theme: "monokai", // Editor theme
      lint: true, // Turn on grammar checking
    });
    editor.setSize("100%", "100%");
    editor.on("change", (cm) => {
      if (cm.getValue() !== this.contextMapYaml) {
        this.$emit("onContextMapChanges", cm.getValue());
      }
    });
  },
});
</script>

<style></style>
