<template>
  <div @wheel.alt.prevent="zoom" class="draw-area h-100"></div>
</template>

<script lang="ts">
import {
  mxCell,
  mxConstants,
  mxEditor,
  mxEventObject,
  mxGeometryChange,
  mxGraph,
} from "mxgraph";
import { defineComponent, PropType, shallowReactive } from "vue";
import mx from "./mxGraphUtils";
import { Context, ContextMap } from "../model/ContextMap";
import { vertexId, ContextCell } from "./ContextCell";

interface State {
  visible: boolean;
}

const getContextCellParent = (
  cell: mxCell | undefined
): ContextCell | undefined => {
  if (!cell) {
    return undefined;
  } else if (cell instanceof ContextCell) {
    return cell;
  } else if (cell.parent) {
    return getContextCellParent(cell.parent);
  } else {
    return undefined;
  }
};

const highlightCell = (graph: mxGraph, cells: mxCell[], opacity: number) => {
  const allCells = [...cells, ...cells.flatMap((cell) => cell.children)];

  graph.setCellStyles(mx.mxConstants.STYLE_FILL_OPACITY, opacity, allCells);
  graph.setCellStyles(mx.mxConstants.STYLE_TEXT_OPACITY, opacity, allCells);
  graph.setCellStyles(mx.mxConstants.STYLE_STROKE_OPACITY, opacity, allCells);
};

const editor: mxEditor = new mx.mxEditor();

export default defineComponent({
  name: "Diagram",
  data(): State {
    return shallowReactive({
      visible: true,
    });
  },
  props: {
    contextMap: {
      type: Object as PropType<ContextMap>,
      required: true,
    },
  },
  mounted() {
    if (!mx.mxClient.isBrowserSupported()) {
      mx.mxUtils.error("Browser is not supported!", 200, false);
    } else {
      const container = this.$el;

      editor.setGraphContainer(container);

      const graph = editor.graph;
      graph.isCellFoldable = (): boolean => false;
      graph.isCellEditable = (): boolean => false;
      graph.isCellMovable = (cell: mxCell): boolean => cell.isVertex();
      const superIsCellSelectable = graph.isCellSelectable;
      graph.isCellSelectable = (cell: mxCell) => {
        return (
          superIsCellSelectable.apply(graph, [cell]) &&
          !cell.isEdge() &&
          cell instanceof ContextCell
        );
      };
      graph.model.addListener(mx.mxEvent.CHANGE, this.handleMxChange);
      graph.addListener(mx.mxEvent.CLICK, this.handleMxClick);
      
      // startArrow=none;endArrow=none;strokeWidth=2;strokeColor=#1B1B1B;
      const style = graph.getStylesheet().getDefaultEdgeStyle();
      style[mx.mxConstants.STYLE_STARTARROW] = mx.mxConstants.NONE;
      style[mx.mxConstants.STYLE_ENDARROW] = mx.mxConstants.NONE;
      style[mx.mxConstants.STYLE_STROKEWIDTH] = "2";
      style[mx.mxConstants.STYLE_STROKECOLOR] = "#1B1B1B";

      this.updateGraph(this.renderContextMap);
    }
  },
  updated() {
    this.updateGraph((graph) => {
      graph.model.clear();
    });
    this.updateGraph(this.renderContextMap);
  },
  methods: {
    handleMxChange(_: mxGraph, evt: mxEventObject) {
      evt
        .getProperty("edit")
        .changes.filter(
          (change: Record<string, unknown>) =>
            change.constructor.name === "mxGeometryChange"
        )
        .forEach((change: mxGeometryChange) => {
          const geometryChange: mxGeometryChange = change;

          const contextCell = getContextCellParent(geometryChange.cell);
          contextCell &&
            this.$emit("onContextPositionChanges", {
              contextId: contextCell.contextId,
              position: {
                x: geometryChange.geometry.x,
                y: geometryChange.geometry.y,
              },
            });
        });
    },
    handleMxClick(graph: mxGraph, evt: mxEventObject) {
      const cell = getContextCellParent(evt.getProperty("cell"));
      const parent = graph.getDefaultParent();
      if (cell) {
        this.updateGraph(() => {
          const relatedEdges = cell.children
            .filter((child: mxCell) => child.isVertex())
            .flatMap((child: mxCell) => graph.getEdges(child));
          const relatedVertices = relatedEdges
            .map((edge) =>
              getContextCellParent(edge.source) === cell
                ? edge.target
                : edge.source
            )
            .filter((vertex) => !!vertex)
            .flatMap((vertex) => {
              if (vertex instanceof ContextCell) {
                return [vertex];
              } else {
                const contextCell = getContextCellParent(vertex);
                return !contextCell
                  ? [vertex]
                  : [vertex, contextCell.contextVertex];
              }
            });

          const allCells = graph
            .getChildCells(parent)
            .flatMap((child: mxCell) =>
              mxCell instanceof ContextCell ? mxCell.children : [child]
            );

          highlightCell(graph, allCells, 50);
          highlightCell(
            graph,
            [cell, ...relatedEdges, ...relatedVertices],
            100
          );
        });
        this.$emit("onSelectContext", this.getAccordingContext(cell));
      } else {
        this.$emit("onSelectContext", null);
        highlightCell(graph, graph.getChildCells(parent), 100);
      }
    },
    zoom(e: WheelEvent) {
      if (e.deltaY > 0) {
        editor?.graph?.zoomIn();
      } else {
        editor?.graph?.zoomOut();
      }
    },
    getAccordingContext(cell: ContextCell): Context {
      return this.contextMap.contexts.filter(
        (ctx) => ctx.id === cell.contextId
      )[0];
    },
    renderContextMap(graph: mxGraph) {
      const model = graph.model;
      const parent = graph.getDefaultParent();

      const currentContexts = new Set<string>();
      const currentRelations = new Set<string>();
      this.contextMap.contexts.forEach((ctx: Context) => {
        currentContexts.add(ctx.id);
        drawContext(ctx);
      });

      this.contextMap.contexts.forEach((ctx: Context) => {
        drawRelations(ctx).forEach((el) => currentRelations.add(el));
      });

      // cleanup
      const cleanup: Array<mxCell> = [];
      model.getChildVertices(parent).forEach((vtx) => {
        if (vtx instanceof ContextCell && !currentContexts.has(vtx.contextId)) {
          cleanup.push(vtx);
        }
      });

      model.getChildEdges(parent).forEach((vtx) => {
        if (!currentRelations.has(vtx.getId())) {
          cleanup.push(vtx);
        }
      });

      graph.removeCells(cleanup);
    },
    updateGraph(callback: (graph: mxGraph) => void) {
      const _graph: mxGraph = editor?.graph ?? throwNotDefined<mxGraph>();
      const model = _graph.getModel();
      model.beginUpdate();
      try {
        callback.apply(this, [_graph]);
      } catch (e) {
        console.error(e);
      } finally {
        // Updates the display
        model.endUpdate();
        // _graph.refresh();
      }
    },
  },
});

function drawContext(ctx: Context) {
  const graph: mxGraph = editor?.graph ?? throwNotDefined();
  const model = graph.model;

  const vtxId = vertexId(ctx.id);

  if (!model.getCell(vtxId)) {
    const cell = new ContextCell(ctx);
    graph.addCell(cell);
  } else {
    const cell = model.getCell(vtxId) as ContextCell;
    cell.update(ctx);
  }
}

function drawRelations(ctx: Context): Set<string> {
  const drawnRelations = new Set<string>();
  const graph: mxGraph = editor?.graph ?? throwNotDefined();
  const model = graph.model;

  const parent = graph.getDefaultParent();
  ctx.relations?.forEach((rel) => {
    const fromVtxId = vertexId(ctx.id);
    const toVtxId = vertexId(rel.target);
    const fromCell = model.getCell(fromVtxId) as ContextCell | null;
    const targetCell = model.getCell(toVtxId) as ContextCell | null;

    if (fromCell && targetCell) {
      let to: mxCell = targetCell.contextVertex;
      let from: mxCell = fromCell.contextVertex;
      const id = `${ctx.id}-${rel.target}`;

      if (rel.targetType) {
        to =
          targetCell.getRelationTerminal(fromCell, rel.targetType) ??
          throwNotDefined();
      } else {
        // TODO does not work ATM
        // targetCell.removeRelationTerminal(from)
      }

      if (rel.sourceType) {
        from =
          fromCell.getRelationTerminal(targetCell, rel.sourceType) ??
          throwNotDefined();
      } else {
        // TODO does not work ATM
        // targetCell.removeRelationTerminal(from)
      }

      if (!model.getCell(id)) {
        const edge = graph.insertEdge(
          parent,
          id,
          "",
          from,
          to,
          "" //startArrow=none;endArrow=none;strokeWidth=2;strokeColor=#1B1B1B;" //entryX=0.5;entryY=0.5;exitX=0.5;exitY=0.5;exitPerimeter=0;entryPerimeter=0;"
        );
        graph.orderCells(true, [edge]);
        graph.insertVertex(
          edge,
          null,
          "U",
          0.7,
          0.0,
          15,
          15,
          "fontSize=16;fontColor=#000000;fillColor=#ffffff;strokeColor=none;",
          true
        );
        graph.insertVertex(
          edge,
          null,
          "D",
          -0.7,
          0,
          10,
          10,
          "fontSize=16;fontColor=#000000;fillColor=#ffffff;strokeColor=none;",
          true
        );
      }
      drawnRelations.add(id);
    }
  });
  return drawnRelations;
}

function throwNotDefined<T>(): T {
  throw new Error("");
}
</script>

<style>
div.draw-area {
  /* background-image: url("/grid.gif"); */
  height: 100%;
}
</style>
