import { createApp, reactive } from "vue";
import App from "./App.vue";

import { Context, ContextMap, VizContextPosition } from "./model/ContextMap";
import yaml from "js-yaml";
import _ from "lodash";
// import "bootstrap/scss/bootstrap.scss";

window.jsyaml = yaml;

const parseYaml = (
  yamlStr: string
): { contextMap: ContextMap; error?: Error } => {
  try {
    const newMap: ContextMap = yaml.load(yamlStr) as ContextMap;

    return { contextMap: newMap };
  } catch (e) {
    console.log("Invalid YAML");
    return {
      contextMap: {
        contexts: [],
      },
      error: e,
    };
  }
};

export interface State {
  contextMap: ContextMap;
  selectedContextId: string | null;
  contextMapYaml: string;
}

export interface ContextPositionUpdate {
  contextId: string;
  position: VizContextPosition;
}

export interface Store {
  state: State;
  updateContextMap: (newValue: string) => void;
  handleContextSelected: (context: Context | null) => void;
  handleContextPositionChanged: (move: ContextPositionUpdate) => void;
}

class StoreImpl implements Store {
  private debug = false;

  state = reactive<State>({
    contextMapYaml: startYaml,
    contextMap: parseYaml(startYaml).contextMap,
    selectedContextId: null,
  });

  private updateYaml(newValue: string) {
    this.state.contextMapYaml = newValue;
    window.localStorage.setItem("lastMap", newValue);
  }

  updateContextMap(newValue: string) {
    if (this.debug) {
      console.log("setMessageAction triggered with", newValue);
    }

    const { contextMap, error } = parseYaml(newValue);
    if (!error) {
      this.state.contextMap = contextMap;
    }
    this.updateYaml(newValue);
  }

  handleContextSelected(context: Context | null) {
    this.state.selectedContextId = context?.id ?? null;
  }

  handleContextPositionChanged(move: ContextPositionUpdate) {
    if (this.debug) {
      console.log("handleContextPositionChanged triggered with", move);
    }

    const newMap: ContextMap = _.cloneDeep(this.state.contextMap);
    const ctx = newMap.contexts.find((it) => it.id === move.contextId);
    if (ctx) {
      const currentMetadata = ctx["x-viz-metadata"];
      ctx["x-viz-metadata"] = {
        ...currentMetadata,
        position: {
          x: move.position.x < 0 ? 0 : move.position.x,
          y: move.position.y < 0 ? 0 : move.position.y,
        },
      };

      // const rawContext = toRaw(newMap)
      try {
        const yamlString = yaml.dump(newMap, {
          skipInvalid: true,
          lineWidth: -1,
        });
        this.updateYaml(yamlString);
        this.state.contextMap = newMap;
        console.log(["Updated Position", move, newMap, yamlString]);
      } catch (e) {
        console.log(e);
      }
    }
  }
}

const startYaml: string =
  window.localStorage.getItem("lastMap") ??
  "---\ncontexts:\n" +
    "  - id: test\n" +
    "    name: test\n" +
    "    relations:\n" +
    "      - target: test2\n" +
    "  - id: test2\n" +
    "    name: test-2";

const store = new StoreImpl();

const app = createApp(App, {
  store,
});
// app.use(PrimeVue);
app.mount("#app");
