<template>
  <nav class="navbar">
    <div class="container-fluid">
      <div class="brand">Context-Mapper</div>
    </div>
  </nav>
  <div class="content">
    <div class="dev">
      <div class="editor">
        <Editor
          :contextMapYaml="contextMapYaml"
          @onContextMapChanges="handleContextMapChanged"
        />
      </div>
      <div class="diagram">
        <Diagram
          :contextMap="contextMap"
          @onSelectContext="handleContextSelected"
          @onContextPositionChanges="handleContextPositionChanged"
        />
      </div>
    </div>
    <div class="markdown">    
      <Markdown
        :value="selectedContext?.description"
        v-if="showDescription"
      />      
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType, readonly, toRefs } from "vue";
import Diagram from "./components/Diagram.vue";
import Editor from "./components/Editor.vue";
import Markdown from "./components/Markdown.vue";
import { ContextPositionUpdate, Store } from "./main";
import { Context } from "./model/ContextMap";

export default defineComponent({
  name: "App",
  components: {
    Diagram,
    Editor,
    Markdown,
  },
  data() {
    const { contextMap, contextMapYaml } = toRefs(this.$props.store.state);
    return {
      contextMap: readonly(contextMap),
      contextMapYaml: readonly(contextMapYaml),
    };
  },
  computed: {
    showDescription: function (): boolean {
      return !!this.selectedContext?.description;
    },
    selectedContext: function (): Context | null {
      const selectedId = this.$props.store.state.selectedContextId;
      if (!selectedId) {
        return null;
      } else {
        return (
          this.$props.store.state.contextMap.contexts.find(
            (ctx) => ctx.id === selectedId
          ) ?? null
        );
      }
    },
  },
  methods: {
    handleContextMapChanged: function (updatedContextMap: string) {
      this.store.updateContextMap(updatedContextMap);
    },
    handleContextSelected(context: Context | null) {
      this.store.handleContextSelected(context);
    },
    handleContextPositionChanged(move: ContextPositionUpdate) {
      this.store.handleContextPositionChanged(move);
    },
  },
  props: {
    store: {
      type: Object as PropType<Store>,
      required: true,
    },
  },
});
</script>

