import { mxCell, mxGeometry } from "mxgraph";
import mx from "./mxGraphUtils";
import { Context, VisualisationMetaData } from "../model/ContextMap";

export const vertexId = (id: string): string => `_ctx_${id}`;

const defaultSize = 100;
const defaultMargin = 10;

const labelheight = 20;
const minLabelLength = 3;
const minLabelWidth = 30;
const labelLengtsPerCharacter = 10;

const createGeometry = (vis: VisualisationMetaData): mxGeometry => {
  return new mx.mxGeometry(
    vis.position?.x,
    vis.position?.y,
    defaultSize + 2 * defaultMargin,
    defaultSize + 2 * defaultMargin
  );
};

const defaulfStyle: VisualisationMetaData = {
  backgroundColor: "#FD506A",
  fontColor: "#1B1B1B",
  strokeColor: "#1B1B1B",
  position: { x: 0, y: 0 },
};

export class ContextCell extends mx.mxCell {
  readonly contextVertex: mxCell;
  readonly contextId: string;

  constructor(context: Context) {
    super(
      null,
      createGeometry({ ...defaulfStyle, ...context["x-viz-metadata"] }),
      `${mx.mxConstants.STYLE_OPACITY}=0;`
    );
    this.contextId = context.id;
    this.id = vertexId(context.id);
    this.vertex = true;

    this.contextVertex = new mx.mxCell(
      context.name ?? context.id,
      new mx.mxGeometry(defaultMargin, 10, defaultSize, defaultSize),
      this.generateStyleString(context)
    );
    this.contextVertex.id = `${this.id}.contextVertex`;
    this.contextVertex.parent = this;
    this.contextVertex.vertex = true;

    this.insert(this.contextVertex, this.getChildCount());
  }

  private generateStyleString = (context: Context): string => {
    const vis = { ...defaulfStyle, ...context["x-viz-metadata"] };
    let style = "shape=ellipse;fontStyle=1";
    style += `;${mx.mxConstants.STYLE_FILLCOLOR}=${vis.backgroundColor}`;
    style += `;${mx.mxConstants.STYLE_FONTCOLOR}=${vis.fontColor}`;
    style += `;${mx.mxConstants.STYLE_STROKECOLOR}=${vis.strokeColor}`;
    return style;
  };

  update(context: Context): void {
    this.contextVertex.valueChanged(context.name ?? context.id);
    this.contextVertex.setStyle(this.generateStyleString(context));
  }

  removeRelationTerminal(target: ContextCell): void {
    const id = `${this.id}.${target.id}`;

    const child = this.getChild(id);
    if (child) {
      this.remove(this.getIndex(child));
    }
  }

  getRelationTerminal = (target: ContextCell, label: string): mxCell => {
    const p1 = { x: this.getGeometry().x, y: this.getGeometry().y };
    const p2 = { x: target.getGeometry().x, y: target.getGeometry().y };
    const targetAbove = p1.y > p2.y;

    const v1 = { x: p1.x - p2.x, y: p1.y - p2.y };
    const v2 = { x: -1, y: 0 };

    const scal = v1.x * v2.x + v1.y * v2.y;

    const l1 = Math.sqrt(Math.pow(v1.x, 2) + Math.pow(v1.y, 2));
    const l2 = Math.sqrt(Math.pow(v2.x, 2) + Math.pow(v2.y, 2));

    const angleRad = Math.acos(scal / (l1 * l2));

    const angleDeg = Math.round(angleRad * (180 / Math.PI));
    // console.log({p1, p2, v1, v2, scal, length1: l1, length2: l2, angle: angleDeg, center: this.getGeometry(), relation: `${this.contextId}-${target.contextId}`})

    const id = `${this.id}.${target.id}`;

    const child = this.getChild(id);

    const labelWidth =
      label.length < minLabelLength
        ? minLabelWidth
        : label.length * labelLengtsPerCharacter;
    const geo = new mx.mxGeometry(
      118 - labelWidth / 2,
      50,
      labelWidth,
      labelheight
    );

    geo.rotate(
      targetAbove ? 360 - angleDeg : angleDeg - 360,
      new mx.mxPoint(
        this.contextVertex.getGeometry().getCenterX(),
        this.contextVertex.getGeometry().getCenterY()
      )
    );

    const labelRotation = targetAbove ? 90 - angleDeg : angleDeg - 90;
    // const style = `fillColor=#5BCCAF;strokeColor=#1B1B1B;rotation=${labelRotation};`;
    const style = `fillColor=#60C487;strokeColor=#1B1B1B;rotation=${labelRotation};`;
    if (child) {
      child.setStyle(style);
      child.setGeometry(geo);
      return child;
    }

    const ohsVertex = new mx.mxCell(label, geo, style);
    ohsVertex.id = id;
    ohsVertex.parent = this;
    ohsVertex.vertex = true;
    this.insert(ohsVertex, this.getChildCount());
    return ohsVertex;
  };

  // private static OPACITY_CONSTANTS = [
  //     mx.mxConstants.STYLE_FILL_OPACITY,
  //     mx.mxConstants.STYLE_TEXT_OPACITY,
  //     mx.mxConstants.STYLE_STROKE_OPACITY,
  // ]
  // highlight = (opacity: number) => {
  //     [this, ...this.children].forEach((cell: mxCell) => {
  //         cell.setStyle(ContextCell.OPACITY_CONSTANTS.map(constant => (`${constant}=${opacity}`)).join(";"))
  //     })
  // }

  private getChild = (id: string): mxCell | undefined => {
    const found = this.children.filter((it) => it?.id === id);
    if (found.length == 0) {
      return undefined;
    }
    return found[0];
  };
}
